<template>
  <div class="kosuljica">
    <md-table md-card v-model="curr.content">
      <!-- <md-table-toolbar>
        <h1 class="md-title">{{curr.name}}</h1>
      </md-table-toolbar>

    <md-table-row slot='md-table-row' slot-scope='{ item }' md-selectable="single" :class="getClass(item.status)">
      <md-table-cell md-label='Ime Filma'>
        {{item.ime}}
      </md-table-cell>
      </md-table-row>-->

      <md-table-row slot="md-table-row">
        <md-table-head>Ime Filma</md-table-head>
        <md-table-head>Autor</md-table-head>
        <md-table-head>Genre</md-table-head>
        <md-table-head>Bodovi</md-table-head>
        <md-table-head>Actions</md-table-head>
      </md-table-row>

      <md-table-row
        class="entry"
        v-bind:key="film.ime"
        v-for="film in curr.content"
        v-bind:class="{'active': film.status==0, 'complete': film.status>=2}"
      >
        <md-table-cell>
          <md-autocomplete v-model="entry" :md-options="films" md-layout="box" v-if='editing == film.id'>
          </md-autocomplete>
          <label v-else>
            {{film.ime}}
          </label>
        </md-table-cell>
        <md-table-cell>
          Autor {{film.id}}
        </md-table-cell>
        <md-table-cell>
          Genre {{film.id}}
        </md-table-cell>
        <md-table-cell>
          {{film.id}}
        </md-table-cell>
        <md-table-cell>
          <md-button @click="edit(film.id)"><label v-if='editing == film.id'>SAVE</label><label v-else>EDIT</label></md-button>
        </md-table-cell>
      </md-table-row>
    </md-table>

    <br />
    <md-button @click="backEntry">Back</md-button>
    <md-button @click="saveEntry">Save</md-button>
  </div>
</template>

<script>
export default {
  name: "Kosuljica",
  data: () => ({
    id: null,

    editing: null,
    entry: null,

    films: ["Film 1", "Film 2", "Film 3", "Film 4", "Film 5", "Aaaa", "Ffff"]

  }),
  methods: {
    backEntry() {
      this.$store.commit("setKosuljica", this.id);
      this.$router.go(-1);
    },

    saveEntry() {
      this.backEntry();
    },

    getClass: status => ({
      "md-primary": status === 2,
      "md-accent": status === 0
    }),

    edit(idcurr) {
      if(this.editing != idcurr){
        this.editing = idcurr;
        this.entry = this.curr.content[idcurr%3].ime;
      }
      else {
        this.$store.commit('changeEntry', {id: idcurr, newEntry: this.entry});
        this.editing = null;
        this.entry = null;
      }
    }
  },
  computed: {
    curr() {
      return this.$store.state.kos[this.id];
    }
  },
  created() {
    this.id = this.$route.params.id;
  }
};
</script>

<style scoped lang='scss'>
.entry {
  margin: 5px;
}

.active {
  background: #ff5252;
}
.complete {
  background: rgb(133, 255, 144);
}

.md-field {
  margin-bottom: 4px;
}

</style>